<template>
  <div class="history-div">
    <v-data-table
    :headers="headers"
    :items="delivered_tanks_list"
    :items-per-page="5"
    class="elevation-1"
  ></v-data-table>
  </div>
</template>
<script>
import {mapState} from 'vuex';
export default {
  name: 'History',
  components: {
  },
  data() {
    return{
      headers: [
          {
            text: 'Nazwa',
            align: 'start',
            sortable: false,
            value: 'name',
          },
          { text: 'Miasto', value: 'city' },
          { text: 'Ulica', value: 'street' },
        ],
    }
  },
  created() {
    if (!this.$store.getters['authentication/isLoggedIn']) {
      this.$router.push('/login');
    }
    this.$store.dispatch('common/loadDeliveredTanksList');
  },
  computed: {
...mapState('common', [
      'delivered_tanks_list',
    ])
  }
}
</script>